.navbar {
	position: fixed;
	width: calc(100% - 52px);
	top: 0;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 16px 32px;
	z-index: 1;
}

.navbar__poster img {
	width: 120px;
	object-fit: contain;
}

.navbar__profile {
	display: flex;
	align-items: center;
}

.navbar__profile img {
	padding: 0px 12px;
	cursor: pointer;
}

.navbar_profile img.profile__icon {
	width: 40px;
}
.profile__icon {
	cursor: pointer;
	padding: 10px 20px;
	border-radius: 7px;
	background: red;
}

.navbar_poster img.profile_search {
	width: 24px;
	background-color: aqua;
}